.container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f8f8f8;
}

h2 {
  margin-bottom: 20px;
}

form {
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 5px;
}

input[type="text"],
input[type="password"],
button {
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

button {
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

p {
  margin-top: 10px;
}

p a {
  color: #007bff;
  text-decoration: none;
}

p a:hover {
  text-decoration: underline;
}
.header-container {
  background-color: #007bff;
  padding: 10px 0;
  text-align: left;
  color: white;
}

.app-name {
  font-size: 24px;
  font-weight: bold;
}
.app-logo {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}
.navbar {
  background-color: #007bff;
  padding: 10px 20px;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar li {
  margin-right: 10px;
}

.navbar a {
  color: white;
  text-decoration: none;
}

.navbar a:hover {
  text-decoration: underline;
}
/* App Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.header {
  display: flex;
  align-items: center;
  background-color: #007bff; /* Change background color as needed */
  padding: 10px 20px;
}

.logo {
  width: 50px; /* Adjust logo width */
  height: 50px; /* Adjust logo height */
  margin-right: 10px;
}

.app-name {
  color: white; /* Text color for app name */
}

nav ul {
  list-style-type: none;
  display: flex;
}

nav li {
  margin-right: 10px;
}

nav a {
  text-decoration: none;
  color: #333; /* Default link color */
}

nav a:hover {
  text-decoration: underline;
  color: #007bff; /* Hover color for links */
}
